<template>
  <div class="submit-form">
    <div v-if="!submitted">
      <div class="form-group">
        <label for="name">Name</label>
        <input
          type="text"
          class="form-control"
          id="name"
          required
          v-model="owner.name"
          name="name"
        />
      </div>

      <div class="form-group">
        <label for="age">Age</label>
        <input
          class="form-control"
          id="age"
          required
          v-model="owner.age"
          name="age"
        />
      </div>

      <button @click="saveOwner" class="btn btn-success">Submit</button>
    </div>

    <div v-else>
      <h4>You submitted successfully!</h4>
      <button class="btn btn-success" @click="newOwner">Add</button>
    </div>
  </div>
</template>

<script>
import OwnerDataService from "../services/OwnerDataService";

export default {
  name: "add-owner",
  data() {
    return {
      owner: {
        id: null,
        name: "",
        age: "",

      },
      submitted: false
    };
  },
  methods: {
    saveOwner() {
      var data = {
        name: this.owner.name,
        age: this.owner.age
      };

      OwnerDataService.create(data)
        .then(response => {
          this.owner.id = response.data.id;
          console.log(response.data);
          this.submitted = true;
        })
        .catch(e => {
            console.log("Error after attempt to create")
          console.log(e);
        });
    },

    newOwner() {
      this.submitted = false;
      this.owner = {};
    }
  }
};
</script>

<style>
.submit-form {
  max-width: 300px;
  margin: auto;
}
</style>